import axios from 'axios'

export default {
  actions: {
     AuthMedics({commit},dataAccess){
      axios.post('http://83.239.111.231/api/authmedics',{dataAccess}).then((response) => { const data = response.data;
        console.log(data);  
      commit('UpdateRulesMedics',data.data[0].rules);        
    }).catch((error) => {
      if(error.response ){
          console.log(error.response.data); // => the response payload
      }
  }); 
     },
     DownloadDevisions({commit},dataAccess){
      axios.post('http://83.239.111.231/api/getdivisions',{dataAccess}).then((response) => { const data = response.data;
        commit('UpdateDivisions',data);        
    }).catch((error) => {
      if(error.response ){
          console.log(error.response.data); // => the response payload
      }
  }); 
     }
},
  mutations: {
    ExitChange(state,data){
        state.Pages_2=data;
    },
    UpdateRulesMedics(state,data){
        state.RulesMedics=data
    },
    UpdateDivisions(state,data){
      state.Divisions=data;
    }
  },
  state: {
    RulesMedics: "",
    Divisions:[],
  },
  getters: {
    getAuthMedics(state) {
      return state.RulesMedics;
    },
    getDevisions(state){
      return state.Divisions;
    }
  }
}
